'use client';
import React, { useEffect, useRef, useState } from 'react';
import Image from 'next/image';
import styles from './ScrollSection.module.css';

const ScrollSection = ({ onExpandChange }: { onExpandChange?: (expanded: boolean) => void }) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const [isExpanded, setIsExpanded] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      if (!containerRef.current) return;
      const scrollY = window.scrollY;
      const triggerPoint = window.innerHeight * 0.6;

      if (scrollY > triggerPoint && !isExpanded) {
        containerRef.current.classList.add(styles.fullView);
        setIsExpanded(true);
        onExpandChange?.(true);
      } else if (scrollY <= triggerPoint && isExpanded) {
        containerRef.current.classList.remove(styles.fullView);
        setIsExpanded(false);
        onExpandChange?.(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [isExpanded, onExpandChange]);

  const articles = [
    { id: 1, title: "CARTIER CELEBRATES 100 YEARS OF TRINITY", image: "/watches.jpg" },
    { id: 2, title: "TRINITY: A CONTEMPORARY ICON", image: "/caps.jpg" },
    { id: 3, title: "ALL LINKED BY TRINITY", image: "/perfumes.jpg" },
    { id: 4, title: "TRINITY, A SYMBOL OF UNITY", image: "/sunglasses.jpg" },
    { id: 5, title: "TRINITY 100, THE JOURNEY OF TRINITY", image: "/jackets.jpg" },
  ];

  return (
    <div ref={containerRef} className={styles.container}>
      <div className={styles.content}>
        <p className={styles.caption}>CHAPTER ONE</p>
        <h2 className={styles.title}>3 IS THE MAGIC NUMBER</h2>

        <div className={styles.articleRow}>
          {articles.map(article => (
            <div key={article.id} className={styles.articleItem}>
              <div className={styles.imageWrapper}>
                <Image
                  src={article.image}
                  alt=""
                  fill
                  className={styles.image}
                  sizes="(max-width: 768px) 100vw, 33vw"
                />
              </div>
              <p className={styles.articleTitle}>{article.title}</p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ScrollSection;
